.faq {
  position: relative;
  padding: 6rem 0 12rem;
  z-index: 2;

  @media (max-width: $tablet) {
    padding: 30px 0 70px;
  }

  @media (max-width: $mobile) {
    padding: 83px 0 88px;
  }

  &__title {
    position: relative;
    z-index: 2;
    margin: 0 0 5rem;

    @media (max-width: $tablet) {
      margin: 0 0 30px;
    }

    @media (max-width: $mobile) {
      margin: 0 0 15px;
    }
  }

  &__accordeon {
    position: relative;
    z-index: 2;
  }


  &__item {
    width: 100%;
    border: 1px solid $red;
    margin: 0 0 3rem;

    @media (max-width: $tablet) {
      margin: 0 0 25px;

    }

    @media (max-width: $mobile) {
      padding: 0;
      margin: 0 0 14px;
    }
  }

  &__toggler {
    font-family: $mossport;
    background-color: $red;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $purple;
    width: 100%;
    text-transform: uppercase;
    padding: 2rem 2.2rem 1.7rem 3.9rem;
    font-weight: 700;
    font-size: 3.6rem;
    line-height: 1.3;
    border: none;
    cursor: pointer;
    color: $white;

    @media (max-width: $tablet) {
      font-size: 28px;
      text-align: left;

    }

    @media (max-width: $mobile) {
      font-size: 16px;
      padding: 14px 16px 13px;
      line-height: 1.4;
      text-align: left;
      align-items: flex-start;
      gap: 10px;
    }

    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &__icon {
    display: flex;
    width: 5.5rem;
    height: 5.5rem;

    svg {
      align-self: center;
      transition: .2s transform ease-in-out;
      width: 5rem;
      height: 5rem;
    }

    @media (max-width: $tablet) {
      width: 40px;
      height: 40px;
      margin: 0 0 0 50px;

      svg {
        width: 30px;
        height: 30px;
      }
    }

    @media (max-width: $mobile) {
      margin: 0px 0 0 10px;
      width: 18px;
      height: 18px;

      svg {
        width: 18px;
        height: 18px;
      }
    }
  }


  &__content {
    display: none;
    color: $black;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 1.4;
    max-width: 95rem;
    padding: 2.6rem 4rem 2.6rem;


    @media (max-width: $tablet) {
      font-size: 20px;
      line-height: 1.4;
    }

    @media (max-width: $mobile) {
      font-size: 14px;
      padding: 15px;
    }

    &.active {
      display: block;
    }
  }

}
